(function() {
  'use strict';

  angular
    .module('awaa')
    .service('labels', labels);

  /** @ngInject */
  function labels($log, $resource) {
    this.getLabels = function($rootScope, language) {
      var languageFilePath = 'fonts/labels_' + language + '.json';
      $log.debug$log, (languageFilePath);
      $resource(languageFilePath).get(function(data) {
        $rootScope.labels = data;
      });
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('awaa')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1449339525743;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }


    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('awaa')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, $http, $cookies, $location, labels, urlSrv) {
    //Run translation if selected language changes
    $rootScope.labels = function() {
      labels.getLabels($rootScope, $rootScope.selectedLanguage);
    };

    //Init
    $rootScope.selectedLanguage = 'es';
    $rootScope.labels();

    $log.debug('runBlock end');

    var authenticate = function(credentials, callback) {

      var headers = credentials ? {
        authorization: "Basic " + btoa(credentials.user + ":" + credentials.pass)
      } : {};

      $http.get(urlSrv + 'user', {
        headers: headers
      }).success(function(data) {
        if (data.name) {
          $rootScope.authenticated = true;
        } else {
          $rootScope.authenticated = false;
          //$location.path("/login");
          //$rootScope.error = true;
        }
        callback && callback();
      }).error(function() {
        $rootScope.authenticated = false;
        callback && callback();
      });

    }

    authenticate();
    $rootScope.credentials = {};
    $rootScope.login = function() {
      $rootScope.credentials._csrf = $cookies.get('XSRF-TOKEN');
      $http.post(urlSrv + 'login', $.param($rootScope.credentials), {
        headers: {
          "content-type": "application/x-www-form-urlencoded"
        }
      }).success(function(data) {
        authenticate(function() {
          if ($rootScope.authenticated) {
            $log("Login succeeded");
            $location.path("/");
            $rootScope.error = false;
          } else {
            $log("Login failed")
            $location.path("/login");
            $rootScope.error = true;
          }
        });
      }).error(function(data) {
        $location.path("/login");
        $rootScope.error = true;
        $rootScope.authenticated = false;
      })
    };


    $rootScope.logout = function() {
      $http.post(urlSrv + 'logout', {}).success(function() {
        $rootScope.authenticated = false;
        $location.path("/");
      }).error(function() {
        $rootScope.authenticated = false;
      });
    }



    $http.get(urlSrv + 'token').success(function(token) {
      $http({
        url: 'http://localhost:8080/awaa-web',
        method: 'GET',
        headers: {
          'X-Auth-Token': token.token
        }
      }).success(function(data) {
        $rootScope.greeting = data;
      });
    })
  }

})();

(function() {
  'use strict';

  angular
    .module('awaa')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      }).state('login', {
        url: '/login',
        templateUrl: 'app/components/login/login.html',
        controller: 'LoginController'
      }).state('forgot', {
        url: '/forgot',
        templateUrl: 'app/components/forgot/forgot.html',
        controller: 'ForgotCtrl'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

